import React, { useState, useEffect } from "react";
import { data, Link, useNavigate, useParams } from "react-router-dom";
import EditBlogForm from "./EditBlogForm";
import {
  Save,
  ArrowRight,
  Image,
  Plus,
  X,
  Loader,
  CheckCircle,
  AlertCircle,
  Trash2,
} from "lucide-react";
import { motion } from "framer-motion";
import SeoChecker from "../SeoChecker";
import toast from "react-hot-toast";

const EditBlog = () => {
  const { blogID } = useParams();
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState("");
  const [isUploadingImage, setIsUploadingImage] = useState(false);
  const [seoStatus, setSeoStatus] = useState({
    titleLength: {
      status: "warning",
      message: "عنوان خیلی کوتاه است (حداقل 30 کاراکتر)",
    },
    contentLength: {
      status: "error",
      message: "محتوا حداقل باید 300 کلمه باشد",
    },
    keywordDensity: {
      status: "warning",
      message: "تراکم کلمات کلیدی مناسب نیست",
    },
    metaDescription: { status: "error", message: "توضیحات متا تعریف نشده است" },
    headings: {
      status: "warning",
      message: "از عناوین H2 و H3 بیشتری استفاده کنید",
    },
    images: { status: "error", message: "تصویر شاخص ندارد" },
    internalLinks: { status: "warning", message: "لینک داخلی کافی نیست" },
  });

  const [allBlogs, setAllBlogs] = useState([]);

  const [formData, setFormData] = useState({
    id: null,
    title: "",
    content: "",
    excerpt: "",
    meta_description: "",
    featured_image: null,
    keywords: "",
    status: "draft",
    tags: tags,
  });

  const [seoScore, setSeoScore] = useState({
    score: 0,
    color: "text-red-500",
  });

  // Fetch blog data based on ID
  useEffect(() => {
    const fetchBlogData = async () => {
      setIsLoading(true);
      try {
        const response = await fetch(
          `http://127.0.0.1:8000/blog/?id=${blogID}`
        );
        const data = await response.json();
        const blog = data[0];

        setFormData({
          id: blog?.id,
          title: blog?.title || "",
          content: blog?.content || "",
          excerpt: blog?.excerpt || "",
          meta_description: blog?.meta_description || "",
          featured_image: blog?.featured_image, // ✅ set it here
          keywords: blog?.keywords || "",
          status: blog?.status || "draft",
          tags: tags || [],
          category: blog?.category?.category,
          author: blog?.author,
        });

        let tagsItem = blog.tags.map((tag) => tag.name);

        setTags(tagsItem || []);

        // Update SEO status for image if there's a featuredImageUrl
        if (blog.featured_image) {
          updateSeoStatus("images", "success", "تصویر شاخص اضافه شده است");
        }
      } catch (error) {
        toast.error(error.message);
        // Handle error (show notification, etc.)
      } finally {
        setIsLoading(false);
      }
    };

    fetchBlogData();
  }, [blogID]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const addTag = () => {
    if (tagInput && !tags?.includes(tagInput)) {
      setTags([...tags, tagInput]);
      setTagInput("");
    }
  };

  const removeTag = (tagToRemove) => {
    setTags(tags.filter((tag) => tag !== tagToRemove));
  };

  const handleImageChange = async (e) => {
    const file = e.target.files[0];
    if (file) {
      const imageForm = new FormData();
      imageForm.append("image", file);

      try {
        setIsUploadingImage(true);
        // Upload image
        let imageUrl = null;
        const imageResponse = await fetch(
          `http://127.0.0.1:8000/upload-images/`,
          {
            method: "POST",
            body: imageForm,
          }
        );
        if (!imageResponse.ok) return;

        const imageData = await imageResponse.json();
        imageUrl = `http://127.0.0.1:8000${imageData.image_urls[0]}`;

        setFormData({
          ...formData,
          featured_image: imageUrl,
        });
      } catch (e) {
        toast.error(`!خطایی رخ داد ${e.message}`);
      } finally {
        setIsUploadingImage(false);
      }

      // Update SEO status for image
      updateSeoStatus("images", "success", "تصویر شاخص اضافه شده است");
    }
  };

  const removeImage = () => {
    setFormData({
      ...formData,
      featured_image: null,
    });
    updateSeoStatus("images", "error", "تصویر شاخص ندارد");
  };

  const updateSeoStatus = (key, status, message) => {
    setSeoStatus((prev) => ({
      ...prev,
      [key]: { status, message },
    }));
  };

  const calculateSeoScore = () => {
    // Calculate SEO score based on form data
    let score = 0;
    let totalChecks = Object.keys(seoStatus)?.length;

    // Title check
    if (formData?.title?.length >= 30 && formData?.title?.length <= 60) {
      score += 1;
      updateSeoStatus("titleLength", "success", "طول عنوان مناسب است");
    } else if (formData?.title?.length > 0) {
      updateSeoStatus(
        "titleLength",
        "warning",
        "طول عنوان مناسب نیست (بین 30 تا 60 کاراکتر)"
      );
    }

    // Content length check (rough word count)
    const wordCount = formData?.content?.split(/\s+/).filter(Boolean)?.length;
    if (wordCount >= 300) {
      score += 1;
      updateSeoStatus(
        "contentLength",
        "success",
        `محتوا ${wordCount} کلمه دارد (عالی)`
      );
    } else if (wordCount >= 100) {
      updateSeoStatus(
        "contentLength",
        "warning",
        `محتوا ${wordCount} کلمه دارد (حداقل 300 کلمه توصیه می‌شود)`
      );
    }

    // Meta description check
    if (
      formData?.meta_description?.length >= 120 &&
      formData?.meta_description?.length <= 155
    ) {
      score += 1;
      updateSeoStatus("metaDescription", "success", "توضیحات متا مناسب است");
    } else if (formData?.meta_description?.length > 0) {
      updateSeoStatus(
        "metaDescription",
        "warning",
        "طول توضیحات متا مناسب نیست (بین 120 تا 155 کاراکتر)"
      );
    }

    // Keyword density check - simplified for demo
    if (formData?.keywords && formData?.content) {
      const keywords = formData?.keywords
        .split(",")
        .map((k) => k.trim().toLowerCase());
      let keywordFound = false;

      keywords.forEach((keyword) => {
        if (formData?.content?.toLowerCase().includes(keyword)) {
          keywordFound = true;
        }
      });

      if (keywordFound) {
        score += 1;
        updateSeoStatus(
          "keywordDensity",
          "success",
          "تراکم کلمات کلیدی مناسب است"
        );
      } else {
        updateSeoStatus(
          "keywordDensity",
          "warning",
          "کلمات کلیدی در متن استفاده نشده‌اند"
        );
      }
    }

    // Headings check - simplified, in real implementation would check actual HTML content
    if (
      formData?.content?.includes("<h2>") ||
      formData?.content?.includes("<h3>")
    ) {
      score += 1;
      updateSeoStatus(
        "headings",
        "success",
        "از عناوین H2 و H3 استفاده شده است"
      );
    }

    // Image check
    if (formData?.featured_image) {
      score += 1;
    }

    // Internal links check - simplified
    if (formData?.content?.includes('<a href="/')) {
      score += 1;
      updateSeoStatus("internalLinks", "success", "لینک داخلی استفاده شده است");
    }

    // Calculate percentage
    const percentage = Math.round((score / totalChecks) * 100);

    // Set color based on score
    let color = "text-red-500";
    if (percentage >= 70) color = "text-green-500";
    else if (percentage >= 40) color = "text-yellow-500";

    setSeoScore({
      score: percentage,
      color,
    });
  };

  // Update SEO score on form changes
  useEffect(() => {
    if (!isLoading) {
      calculateSeoScore();
    }
  }, [formData, tags, isLoading]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    try {
      let imageUrl = formData?.featured_image;

      // Upload image if it's a File
      if (formData?.featured_image instanceof File) {
        imageUrl = await uploadImage(formData?.featured_image);
      }

      const updatedFormData = {
        ...formData,
        featured_image: imageUrl,
        tags,
      };

      // Submit updatedFormData to your API
      await fetch(`http://127.0.0.1:8000/blog/update/${blogID}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(updatedFormData),
      });

      navigate("/dashboard/blogs");
    } catch (error) {
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleDelete = (id) => {
    if (window.confirm("آیا مطمئن هستید که می‌خواهید این مقاله را حذف کنید؟")) {
      try {
        fetch("http://127.0.0.1:8000/blog")
          .then((res) => {
            if (!res.ok) return;
            return res.json();
          })
          .then((data) => setAllBlogs(data));

        let removedBlog = allBlogs.filter((blog) => blog.id !== id);
        setFormData(removedBlog);

        fetch(`http://127.0.0.1:8000/blog/?id=${id}`, {
          method: "DELETE",
        })
          .then((res) => {
            if (!res.ok) return;
            toast.success("مقاله با موفقیت پاک شد!");
            return res.json();
          })
      } catch (error) {
      } finally {
        navigate("/dashboard/blogs");
      }
    }
  };

  const uploadImage = async (file) => {
    const formData = new FormData();
    formData?.append("file", file);

    const response = await fetch("http://127.0.0.1:8000/upload-images/", {
      method: "POST",
      body: formData,
    });

    const data = await response.json();
    return data.url; // assuming your API responds with { url: "..." }
  };

  if (isLoading) {
    return (
      <div className="p-6 flex justify-center items-center h-64">
        <div className="flex flex-col items-center">
          <Loader className="w-10 h-10 text-blue-500 animate-spin mb-4" />
          <p className="text-gray-200">در حال بارگذاری اطلاعات مقاله...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-2xl font-bold text-gray-100">ویرایش مقاله</h1>
        <Link
          to="/dashboard/blogs"
          className="flex items-center text-gray-200 hover:text-gray-100"
        >
          <ArrowRight className="w-5 h-5 ml-1" />
          بازگشت به مدیریت بلاگ
        </Link>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Main form */}
        <EditBlogForm
          handleSubmit={handleSubmit}
          formData={formData}
          handleChange={handleChange}
          removeTag={removeTag}
          addTag={addTag}
          tags={tags}
          setTagInput={setTagInput}
          handleImageChange={handleImageChange}
          removeImage={removeImage}
          isSubmitting={isSubmitting}
          handleDelete={handleDelete}
          isUploadingImage={isUploadingImage}
          tagInput={tagInput}
        />

        {/* SEO Score Panel */}
        <SeoChecker
          formData={formData}
          seoScore={seoScore}
          seoStatus={seoStatus}
          updateSeoStatus={updateSeoStatus}
        />
      </div>
    </div>
  );
};

export default EditBlog;
