// SeoChecker.js
import React from 'react';
import { motion } from 'framer-motion';
import { CheckCircle, AlertCircle } from 'lucide-react';

const SeoChecker = ({ 
  formData, 
  seoScore, 
  seoStatus, 
  updateSeoStatus, 
  setSeoScore,
  setSeoStatus 
}) => {
  
  const calculateSeoScore = () => {
    // Calculate SEO score based on form data
    let score = 0;
    let totalChecks = Object.keys(seoStatus).length;

    // Title check
    if (formData.title.length >= 30 && formData.title.length <= 60) {
      score += 1;
      updateSeoStatus("titleLength", "success", "طول عنوان مناسب است");
    } else if (formData.title.length > 0) {
      updateSeoStatus(
        "titleLength",
        "warning",
        "طول عنوان مناسب نیست (بین 30 تا 60 کاراکتر)"
      );
    }

    // Content length check (rough word count)
    const wordCount = formData.content.split(/\s+/).filter(Boolean).length;
    if (wordCount >= 300) {
      score += 1;
      updateSeoStatus(
        "contentLength",
        "success",
        `محتوا ${wordCount} کلمه دارد (عالی)`
      );
    } else if (wordCount >= 100) {
      updateSeoStatus(
        "contentLength",
        "warning",
        `محتوا ${wordCount} کلمه دارد (حداقل 300 کلمه توصیه می‌شود)`
      );
    }

    // Meta description check
    if (
      formData.metaDescription.length >= 120 &&
      formData.metaDescription.length <= 155
    ) {
      score += 1;
      updateSeoStatus("metaDescription", "success", "توضیحات متا مناسب است");
    } else if (formData.metaDescription.length > 0) {
      updateSeoStatus(
        "metaDescription",
        "warning",
        "طول توضیحات متا مناسب نیست (بین 120 تا 155 کاراکتر)"
      );
    }

    // Keyword density check - simplified for demo
    if (formData.keywords && formData.content) {
      const keywords = formData.keywords
        .split(",")
        .map((k) => k.trim().toLowerCase());
      let keywordFound = false;

      keywords.forEach((keyword) => {
        if (formData.content.toLowerCase().includes(keyword)) {
          keywordFound = true;
        }
      });

      if (keywordFound) {
        score += 1;
        updateSeoStatus(
          "keywordDensity",
          "success",
          "تراکم کلمات کلیدی مناسب است"
        );
      } else {
        updateSeoStatus(
          "keywordDensity",
          "warning",
          "کلمات کلیدی در متن استفاده نشده‌اند"
        );
      }
    }

    // Headings check - simplified, in real implementation would check actual HTML content
    if (
      formData.content.includes("<h2>") ||
      formData.content.includes("<h3>")
    ) {
      score += 1;
      updateSeoStatus(
        "headings",
        "success",
        "از عناوین H2 و H3 استفاده شده است"
      );
    }

    // Image check
    if (formData.featuredImage) {
      score += 1;
    }

    // Internal links check - simplified
    if (formData.content.includes('<a href="/')) {
      score += 1;
      updateSeoStatus("internalLinks", "success", "لینک داخلی استفاده شده است");
    }

    // Calculate percentage
    const percentage = Math.round((score / totalChecks) * 100);

    // Set color based on score
    let color = "text-red-500";
    if (percentage >= 70) color = "text-green-500";
    else if (percentage >= 40) color = "text-yellow-500";

    setSeoScore({
      score: percentage,
      color,
    });
  };

  return (
    <div className="col-span-1">
      <div className="bg-white rounded-lg shadow-sm p-6 sticky top-6">
        <h3 className="text-lg font-semibold text-gray-800 mb-4">
          امتیاز سئو
        </h3>

        <div className="flex items-center justify-center mb-6">
          <motion.div
            initial={{ opacity: 0, scale: 0.5 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5 }}
            className={`flex flex-col items-center justify-center w-32 h-32 rounded-full border-4 ${seoScore.color.replace(
              "text-",
              "border-"
            )}`}
          >
            <span className={`text-3xl font-bold ${seoScore.color}`}>
              {seoScore.score}
            </span>
            <span className="text-sm text-gray-500">از 100</span>
          </motion.div>
        </div>

        <div className="space-y-4">
          {Object.keys(seoStatus).map((key) => (
            <div key={key} className="flex items-start">
              {seoStatus[key].status === "success" ? (
                <CheckCircle className="w-5 h-5 text-green-500 mt-0.5 ml-2 flex-shrink-0" />
              ) : seoStatus[key].status === "warning" ? (
                <AlertCircle className="w-5 h-5 text-yellow-500 mt-0.5 ml-2 flex-shrink-0" />
              ) : (
                <AlertCircle className="w-5 h-5 text-red-500 mt-0.5 ml-2 flex-shrink-0" />
              )}
              <div>
                <p
                  className={
                    seoStatus[key].status === "success"
                      ? "text-green-500"
                      : seoStatus[key].status === "warning"
                      ? "text-yellow-500"
                      : "text-red-500"
                  }
                >
                  {seoStatus[key].message}
                </p>
              </div>
            </div>
          ))}
        </div>

        <div className="mt-6 pt-6 border-t border-gray-100">
          <h4 className="font-medium text-gray-700 mb-2">
            نکات بهینه‌سازی:
          </h4>
          <ul className="text-sm text-gray-600 space-y-2">
            <li className="flex items-start">
              <span className="text-green-500 ml-2">•</span>
              از کلمات کلیدی در عنوان، مقدمه و نتیجه‌گیری استفاده کنید
            </li>
            <li className="flex items-start">
              <span className="text-green-500 ml-2">•</span>
              برای عکس‌های خود از alt text مناسب استفاده کنید
            </li>
            <li className="flex items-start">
              <span className="text-green-500 ml-2">•</span>
              از ساختار درست تگ‌های h1، h2 و h3 استفاده کنید
            </li>
            <li className="flex items-start">
              <span className="text-green-500 ml-2">•</span>
              به مقالات مرتبط داخلی لینک دهید
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default SeoChecker;